.input {
  margin-bottom: 20px;
  margin-top: 50px;
  text-align: center;
  display: block;
}
.input input {
  box-sizing: border-box;
  width: 300px;
  height: 50px;
  padding: 10px;
  display: block;
  margin: 30px auto;
  border: none;
  border-radius: 3px;
  transition: width 0.5s;
}

.input input:focus {
  border-color: cyan;
  width: 400px;
}

.input p {
  margin-top: 3vh;
  color: white;
  font-size: 1.5em;
}

.login {
  border: none;
  border-radius: 5px;
  padding: 0.7rem;
  border: 2px solid rgba(0, 0, 0, 0.5);
  cursor: pointer;
  color: white;
  background-color: #1e6ba1;
}

.login:hover {
  border: 2px solid white;
}

.refresh {
  border: 0;
  padding: 0.5rem;
  cursor: pointer;
  border-radius: 3px;
  border: 1px solid rgba(255, 255, 255, 0.5);
  background-color: #f3293a;
}

.refresh:hover {
  background-color: #e63946;
  border-color: white;
  color: white;
  padding: 0.7rem;
}
