form {
  width: 45vw;
  margin: 0 auto;
  height: 100vh;
  color: white;
  /* display: flex;
  justify-content: center; */
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.6);
}

form h1 {
  text-align: center;
  margin-top: 10vh;
  font-size: 3rem;
}
.credentials {
  /* background-color: red; */
  display: flex;
  justify-content: center;
}

.c-wrap {
  /* background-color: blue; */
  text-align: center;
  height: 70vh;
  padding-top: 10vh;
}

.c-wrap label {
  display: block;
  margin-bottom: 10px;
  font-size: 20px;
}

.c-wrap input {
  width: 20vw;
  font-size: small;
  padding: 0.8rem;
  margin-bottom: 20px;
  outline: none;
  border-radius: 25px;
  border: 2.5px solid white;
}

.c-wrap input:focus {
  border-color: #3895d3;
}

.c-wrap button {
  display: block;
}

.c-wrap img {
  margin-top: 5vh;
  width: 15vw;
  border: 1px solid white;
}

.submit-btn {
  width: 70px;
  height: 30px;
  border: none;
  border-radius: 10px;
  margin: 10px auto 20px auto;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: darkgrey;
}
